import Card from "../card/Card";
import ItemCurso from "./ItemCurso";

const Curso = () => {
    return (
        <section id="curso">
            <Card>
                <section id="curso__descricao">
                    <h3>Cursos</h3>
                    <hr /><br />
                    <ul style={{listStyle:"url('./images/correct2.png')"}}>
                        <ItemCurso
                            curso="Auxiliar de Escritório"
                            local="FAETEC"
                            horas="240h"
                        />
                        <ItemCurso
                            curso="Lógica de Programação"
                            local="SENAI"
                            horas="14h"
                        />
                        <ItemCurso
                            curso="Tecnologia da Informação e Comunicação "
                            local="SENAI"
                            horas="14h"
                        />
                    </ul>
                </section>
            </Card>
        </section>
    )
}

export default Curso;