"use client";
import Blog from "@/views/blog/Blog";
import Home from "@/views/home/Home";
import { BrowserRouter, Routes, Route } from "react-router-dom";

const Routing = () => {
    return (
        <BrowserRouter>            
            <Routes>
                <Route path="/" element={<Home />} />
                <Route path="/about" element={<Blog />} />
            </Routes>
        </BrowserRouter>
    )
}

export default Routing;